	module.exports = {"data":{"post":{"id":"/data/projects/work/self/website/content/posts/2018-06-07--generating-entropy-in-linux/index.md absPath of file >>> MarkdownRemark","html":"<p>In Linux, sometimes you may need to generate some entropy.</p>\n<h1>Solutions</h1>\n<p>There are both <em>hardware</em> and <em>software</em> solutions:</p>\n<ul>\n<li><strong>Hardware solutions</strong>: they can <em>generate more entropy per second</em>, but you need physical access</li>\n<li><strong>Software solutions</strong>: they have less performance, but you just need <code class=\"language-text\">root</code> permissions (no physical access)</li>\n</ul>\n<h1>Hardware solutions</h1>\n<p>Typically it’s a USB device that <em>generates and streams some random numbers</em>.</p>\n<p>One of such devices is <a href=\"http://ubld.it/products/truerng-hardware-random-number-generator\">TrueRNG</a> (price is approx: 50$).</p>\n<h1>Software solutions</h1>\n<h2>RNGD</h2>\n<p>This program feeds random data from hardware device to kernel.</p>\n<div class=\"gatsby-highlight\">\n      <pre class=\"language-text\"><code class=\"language-text\">rngd -r &quot;/dev/urandom&quot;</code></pre>\n      </div>\n<p>Here we have used <code class=\"language-text\">rngd</code> program (<a href=\"http://linux.die.net/man/8/rngd\">man 8 rngd</a> for more details).</p>\n<h2>Audio Entropy Daemon</h2>\n<p>This program feeds the <code class=\"language-text\">/dev/random</code> device with entropy-data read from an audio device.</p>\n<p>The audio-data is not copied as is but first <em>de-biased</em> and analysed to determine how much bits of entropy is in it.</p>\n<p>The official website of the program is <a href=\"https://www.vanheusden.com/aed\">here</a>.</p>","fields":{"slug":"/generating-entropy-in-linux/","prefix":"2018-06-07"},"frontmatter":{"title":"Generating Entropy in Linux","subTitle":"A walkthrough on the available solutions for feeding the kernel with some entropy","cover":{"childImageSharp":{"resize":{"src":"/static/entropy-cover-6be686e4ac6c77555b5f598ad154cbe8-ada8c.jpg"}}}}},"author":{"id":"/data/projects/work/self/website/content/parts/author.md absPath of file >>> MarkdownRemark","html":"<p><strong>Alessandro Molari</strong>   Cyber-Security Consultant &#x26; Ethical Hacker, specialized in Red Teaming, Penetration Testing, Advanced Persistent Threat (APT), Vulnerability Research &#x26; Malware Analysis</p>"},"footnote":{"id":"/data/projects/work/self/website/content/parts/footnote.md absPath of file >>> MarkdownRemark","html":"<ul>\n<li>website prodly built by <a href=\"https://alessandro.molari.me\">Alessandro Molari</a></li>\n<li>using the awesome triad\n<a href=\"https://gentoo.org\"><img src=\"/icons/gentoo.svg\" alt=\"Gentoo\"></a> &#x26;\n<a href=\"https://xmonad.org\"><img src=\"/icons/xmonad.svg\" alt=\"XMonad\"></a> &#x26;\n<a href=\"https://www.vim.org\"><img src=\"/icons/vim.svg\" alt=\"Vim\"></a></li>\n</ul>"},"site":{"siteMetadata":{"facebook":{"appId":""}}}},"pathContext":{"slug":"/generating-entropy-in-linux/"}}

